import { Component, Input, OnInit } from '@angular/core';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPSComplexTimeRangeExposed } from './ps-complex-time-range.component.interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';

@Component({
  selector: 'ps-complex-time-range',
  templateUrl: './ps-complex-time-range.component.html',
  styleUrls: ['./ps-complex-time-range.component.scss'],
})
export class PsComplexTimeRangeComponent extends PsBaseFieldComponent implements OnInit {
  @Input() public options: IOptionsPSComplexTimeRangeExposed;
  startdate: any;
  enddate: any;
  errorMsg: string;
  showContainer = false;
  constructor(public commonProv: PsCommonService, loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    super.ngOnInit();
    //added by Marina Jira-AZDB-4549; to check if both time fields are hidden so hide the container 
    const sysParamScreenDispList = this.common.initialScreenDisplayParams.get(PsCommonSettings.oper_ID) ? this.common.initialScreenDisplayParams.get(PsCommonSettings.oper_ID) : new Map<string, any>();
    this.showContainer = sysParamScreenDispList.get(this.options.fromTime.fcName)?.IS_VISIBLE == 1 || sysParamScreenDispList.get(this.options.toTime.fcName)?.IS_VISIBLE == 1;
    this.options.fromTime.labelKey = this.options.fromTime.labelKey ? this.options.fromTime.labelKey : 'time_key';
    this.options.toTime.labelKey = this.options.toTime.labelKey ? this.options.toTime.labelKey : 'time_key';
  }

  fromTimeChanged(value) {
    this.errorMsg = '';
    this.startdate = value.newValue;
    if (this.options.group.controls[ConstantCommon.CONTROLLER_FORM_DATA].value[this.options.toTime.fcName]) {
      this.enddate = this.options.group.controls[ConstantCommon.CONTROLLER_FORM_DATA].value[this.options.toTime.fcName]
      if (CommonUtils.compareTime(this.startdate, this.enddate) != -1) {
        this.errorMsg = this.commonProv.translate('invalid_time_range_key');
        this.options.group.controls[this.options.fromTime.fcName].setValue('');
        this.options.group.controls[ConstantCommon.CONTROLLER_FORM_DATA].value[this.options.fromTime.fcName] = null;

      }
    }
    this.onPsChange.emit(value);
  }
  toTimedChanged(value) {
    this.errorMsg = '';
    this.enddate = value.newValue;
    if (this.options.group.controls[ConstantCommon.CONTROLLER_FORM_DATA].value[this.options.fromTime.fcName]) {
      this.startdate = this.options.group.controls[ConstantCommon.CONTROLLER_FORM_DATA].value[this.options.fromTime.fcName];
      if (CommonUtils.compareTime(this.startdate, this.enddate) != -1) {
        this.errorMsg = this.commonProv.translate('invalid_time_range_key');
        this.options.group.controls[this.options.toTime.fcName].setValue('');
        this.options.group.controls[ConstantCommon.CONTROLLER_FORM_DATA].value[this.options.toTime.fcName] = null;
      }
    }

    this.onPsChange.emit(value);
  }
}
